/* eslint-disable */
export default {
  data() {
    return {
      rolesData: [
        {
          roleName: "2016-05-02",
          roleDesc: "王小虎",
          children: "上海市普陀区金沙江路 1518 弄"
        }
      ],
      dialogAssignRightsVisible: false,
      treeData: [{
        id: 1,
        label: '一级 1',
        children: [{
          id: 4,
          label: '二级 1-1',
          children: [{
            id: 9,
            label: '三级 1-1-1'
          }, {
            id: 10,
            label: '三级 1-1-2'
          }]
        }]
      }, {
        id: 2,
        label: '一级 2',
        children: [{
          id: 5,
          label: '二级 2-1'
        }, {
          id: 6,
          label: '二级 2-2'
        }]
      }, {
        id: 3,
        label: '一级 3',
        children: [{
          id: 7,
          label: '二级 3-1'
        }, {
          id: 8,
          label: '二级 3-2'
        }]
      }],
      defaultProps: {
        // children负责显示结构
        children: 'children',
        // label：负责显示标题
        label: 'authName'
      },
      roleId:0,

    };
  },
  created() {
    this.loadRolesData();
    this.loadAllRightsData();
  },
  methods: {
    // 加载角色数据
    async loadRolesData() {
      let res = await this.$axios.get("roles");
      console.log(res);
      this.rolesData = res.data.data;
    },
    // 获取所有的权限信息
    async loadAllRightsData() {
      let res = await this.$axios.get("rights/tree")
      console.log(res);
      this.treeData = res.data.data
    },
    // 显示对话框，分配权限
    showAssignRightsDialog(row) {
      // 0. 拿到row中的id(角色id)
      this.roleId=row.id;
      // 1. 窗口显示
      this.dialogAssignRightsVisible = true;

      // 2. 获取第三层的id
      let keys=[]
      row.children.forEach(item1=>{
        item1.children.forEach(item2=>{
          item2.children.forEach(item3=>{
            // console.log(item3.id);
            keys.push(item3.id)
          })
        })
      })

      // 3. 设置keys
      //原因异步DOM更新，打印有点早了
      // DOM更新完毕了，再获取
      // nextTick

      this.$nextTick(() => {
        // console.log(this.$refs);
        this.$refs.tree.setCheckedKeys(keys);
      })
    },
    // 分配权限
    async assignRight(){
      // 1. 获取Tree里半选和全选中的id
      let keys1=this.$refs.tree.getHalfCheckedKeys();
      let keys2=this.$refs.tree.getCheckedKeys();
      console.log(keys1)
      let keys=keys1.concat(keys2)
      // 参数一：roleId,当前角色id
      //参数二：rids权限的id
      let res=await this.$axios.post(`roles/${this.roleId}/rights`,{
        rids:keys.join(',')
      })
      console.log(res);
      if(res.data.meta.status===200){
        // 1. 关闭对话框
        // console.log(123);
        this.dialogAssignRightsVisible=false
        // 2. 提示消息
        this.$message({
          message:"关闭成功",
          type:'success',
          duration:800
        })
        //3. 刷新
        this.loadRolesData();
      }
    }
  }
};



